
.li{
 list-style: none;
}
ul{
  list-style: none;
}
.ingresarProd{
  display: flex;  
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 10px;
  margin-bottom: 10px;
}
.login{
  background-color: #dcec47;
  color: black;
  border-radius: 12px;
  border-style: none;
  padding: 7px;
  margin-top: 5px;
  margin: 4px;
}
.inputLog {
 flex-direction: column;
  display: flex;
  justify-content: start;
  max-width: 60px;
  margin: 3px;
  border-radius: 12px;
  border-style: none;
  text-align: center;
}
.inputProd {
  margin-top: 4px;
  margin-bottom: 8px;
  background-color: #dcec47;
  color: black;
  border-style: none;
  text-align: center;
  padding: 4px;
  border-radius: 7px;
}
.stock{
  width: 60px;
}
.precio{
  width: 80px;
}
textarea{
  width: 220px;
}
.subirFoto {
  margin-top: 10px;
}
.App-header {

  
  display: flex;
  align-items: center;
  justify-content: center;
  color: rgb(44, 7, 7);
  margin-top: 5px;
  text-align: center;
  
}
.masOmenos {
  background-color: rgb(102, 192, 102);
  border-style: none;
  padding: 5px;
  border-radius: 8px;
  margin: 4px;
  width: 30px;
  font-weight: 800;
}
.imageFooter{
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  margin-top: 30vh;
  text-align: center;
  margin-bottom: 20px;
  
}
.imageFooter2{
  max-width: 40px;
  max-height: 40px;
  text-align: center;
  margin-bottom: 20px;
}
.mostrarProductos{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 55px;
  margin-right: 8px;
}
.displayFlex{
  display: flex;
  justify-content: center;
  flex-direction: column;
   background-image: linear-gradient(rgba(255, 255, 255, 0.76), rgb(212, 159, 61));
   
}
.displayProductos{
  display: flex;
  justify-content: center;
  flex-direction: column;
 
}
.mostrar {
  display: flex;
  justify-content: center;
 flex-wrap: wrap;
  margin: 17px;
}

.textCenter{
  text-align: center;
  margin-top: 8px;
   margin-bottom: 8px;
   display: flex;
   justify-content: center;
}

.vertical {
  display: flex;
  flex-direction: column;
  position: fixed;
  align-items: center;
  max-width: 80px;
  height: auto;
  position:fixed;
  left: 0;
  padding: 10px;
  top: 0;
  background-color: #61dafb;
  list-style: none;
  border-radius: 7px;
  margin-top: 20px;
  margin-left: 12px;
}
.imagenes{
  border-radius: 8px;
  height: 200px;
  width: 240px;
  object-fit: cover;

}

.carrito{
  display: flex;
  justify-content: center;
  flex-direction: column;
  width: fit-content;
  position: fixed;
  right: 60px;

}
.imgDescrip{
    border-radius: 12px;
  }
  a{
    text-decoration: none;
    color: rgb(79, 13, 105);
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    margin-top: 7px;
    font-weight: 900;
  }
/* Ajuste general para pantallas pequeñas */
@media (max-width: 575px) {
  .App {
    padding: 10px;
  }

  .login {
    width: 60%;
    max-width: 160px;
    text-align: center;
    max-height: 40px;
  }

  .inputLog {
    width: 100%;
    max-width: 180px;
  }

  .mostrarProductos {
    flex-direction: column;
    align-items: center;
    padding-top: 20px;
    width: 100%
 ;
  }

  .vertical {
    position: relative;
    width: 60%;
    max-width: 300px;
    margin: auto;
    padding: 0px;
   background-color: transparent;
  }

  .carrito {
    width: 80px;
    padding: 5px;
    position: fixed;
    right: 20px;
  
  }

  .carrito h2 {
    font-size: 16px;
  }

  .mostrarProductos div {
    width: auto;
    margin: auto;
    
  }

 .imagenes{
  border-radius: 8px;
  height: 200px;
  width: 240px;
  object-fit: cover;

}
  .tamañoCarrito{
    width: 100%;
    height: 100%;
  }
  .imageFooter{
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  margin-top: 5vh;
  text-align: center;
  
}
}